export default {
	data() {
		return {
			list: [],
			name: '',
			CustomBar: this.CustomBar,
			hidden: true,
			listCurID: '',
			listCur: '',
		}
	},
	onLoad() {
		//let list = [{}];

		// for (let i = 0; i < 5; i++) {
		// 	list[i] = {};
		// 	list[i].name = String.fromCharCode(65 + i);
		// }
		//this.list = list;
		this._loadStaffInfo();

	},
	onReady() {
		let that = this;
		uni.createSelectorQuery().select('.indexBar-box').boundingClientRect(function(res) {
			that.boxTop = res.top
		}).exec();
		uni.createSelectorQuery().select('.indexes').boundingClientRect(function(res) {
			that.barTop = res.top
		}).exec()
	},
	methods: {
		_loadStaffInfo: function() {
			//获取商户ID
			let _that = this;
			let _userInfo = this.java110Context.getUserInfo();
			let storeId = _userInfo.storeId;
			let _objData = {
				page: 1,
				row: 15,
				storeId: storeId,
				name: this.name
			};
			this.java110Context.request({
				url: _that.java110Constant.url.queryStaffInfos,
				header: _that.java110Context.getHeaders(),
				method: "GET",
				data: _objData, //动态数据
				success: function(res) {
					console.log("请求返回信息：", res);
					if (res.statusCode == 200) {
						let _data = res.data;
						let _staffs = [];
						console.log('list', _that.list);
						_data.staffs.sort(function(a, b) {
							return (a.initials + '').localeCompare(b.initials + '')
						})
						_that.list = _that._formatList(_data.staffs, 'initials')
						_that.listCur = _that.list[0];
						return;
					}
					wx.showToast({
						title: "服务器异常了",
						icon: 'none',
						duration: 2000
					})
				},
				fail: function(e) {
					wx.showToast({
						title: "服务器异常了",
						icon: 'none',
						duration: 2000
					})
				}
			});

		},
		_callPhoto: function(_photo) {
			uni.makePhoneCall({
				// 手机号
				phoneNumber: _photo,

				// 成功回调
				success: (res) => {
					console.log('调用成功!')
				},

				// 失败回调
				fail: (res) => {
					console.log('调用失败!')
				}

			});
		},
		_searchStaff: function() {
			this._loadStaffInfo();
		},
		_formatList: function(arr, keyword) {
			let newArr1 = [];
			let tempArr = [];
			let reg = /\b(\w)|\s(\w)/g;
			let k = 0;
			let firstWord = arr[0][keyword]; //获取第一个分类字母
			arr.map((v) => {
				if (firstWord == v[keyword]) {
					tempArr.push(v);
					newArr1[k] = {
						initials: firstWord,
						staffs: tempArr
					}
				} else {
					//这里循环到这表示已经第二个字母了
					firstWord = v[keyword]; //设置第二字母
					tempArr = []; //把之前的清除掉
					tempArr.push(v); //添加第一个
					newArr1[++k] = { //自增
						initials: firstWord,
						staffs: tempArr
					}
				}
			});
			return newArr1;
		},
		//获取文字信息
		getCur:function(e) {
			this.hidden = false;
			this.listCur = this.list[e.target.id].initials;
			console.log(this.listCur);
		},
		setCur:function(e) {
			this.hidden = true;
			this.listCur = this.listCur
		},
		//滑动选择Item
		tMove:function(e) {
			let y = e.touches[0].clientY,
				offsettop = this.boxTop,
				that = this;
			//判断选择区域,只有在选择区才会生效
			if (y > offsettop) {
				let num = parseInt((y - offsettop) / 20);
				this.listCur = that.list[num].initials
			};
		},

		//触发全部开始选择
		tStart:function() {
			this.hidden = false
		},

		//触发结束选择
		tEnd:function() {
			this.hidden = true;
			this.listCurID = this.listCur;
		},
		indexSelect:function(e) {
			let that = this;
			let barHeight = this.barHeight;
			let list = this.list;
			let scrollY = Math.ceil(list.length * e.detail.y / barHeight);
			for (let i = 0; i < list.length; i++) {
				if (scrollY < i + 1) {
					that.listCur = list[i].initials;
					that.movableY = i * 20
					return false
				}
			}
		}

	}
}
